import React, { Component } from "react";
import images from "components/images";

class Gallery extends Component {
  constructor(props) {
    super(props);
    this.galleryImages = images.filter((x) => {
      if (x.target === "gallery") {
        return x;
      } else return null;
    });
    this.state = {
      ImageNo: 0,
    };
    this.len = this.galleryImages.length;
  }

  componentDidMount() {
    this.interval = setInterval(() => {
      this.setState({ time: Date.now() });
      this.setState(({ ImageNo }) => ({ ImageNo: (ImageNo + 1) % this.len }));
    }, 3000);
    console.log("cdm");
  }
  componentWillUnmount() {
    clearInterval(this.interval);
    console.log("cwu");
  }

  render() {
    const src = this.galleryImages[this.state.ImageNo].src;
    const name = this.galleryImages[this.state.ImageNo].name;

    return (
      <div>
        This is Gallery
        <div>{this.state.ImageNo}</div>
        <img alt={name} src={src} />
      </div>
    );
  }
}

export default Gallery;
